.thumb{
    width: 340px;
    height: 340px;
    /*border: 1px solid black;*/
    border-radius: 10px;
    padding: 5px;
    background: var(--primary-color);
    background: linear-gradient(0deg, rgba(97,40,40,1) 0%, rgba(255,96,96,1) 20%);
    
}
.thumb h3{
    width: 60%;
    height: 32px;
    text-decoration: none;
    text-align: left;
    margin-left: 15px;
    color: white;
    font-size: medium;
    
}
.thumb img{
    width: 100%;
    height: 270px;
    border-radius: 8px;
    object-fit: cover;
}
.thumb--link {
    text-decoration: none;
    
}
@media only screen and (max-width: 440px){
    .thumb{
        width: 96%;
    }
    .thumb h3{
        width: 100%;
        
    }
}