.Err404 {
    max-width: var(--max-body-width);
    margin: auto;
    color: var(--primary-color);
    text-align: center;
    padding-bottom: 50px;
}

.Err404 h1 {
    
    font-size: 288px;
    margin-top: 80px;
    margin-bottom: -32px;
    font-weight: 700;

    

    
    font-style: normal;
    font-weight: 700;
    font-size: 288px;
    line-height: 142.6%;
    /* or 411px */

    
}

.Err404 p {
    font-size: 36px;
    margin-bottom: 100px;
}
.Err404 .link{
    color: var(--primary-color);
    
}

@media only screen and (max-width: 768px){
    .Err404 h1{
        font-size: 96px;
    }
    .Err404 p{
        font-size: 18px;
    }
}